@media (max-width: 1200px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 992px) {
  .nav {
    &__wrap {
      align-items: center;
    }
    &__burger {
      display: block;
    }
    &__menu {
      display: none;
    }
    .tel {
      display: none;
    }
  }
  .services {
    &__item {
      flex-basis: 50%;
    }
  }
  .price {
    &__grid {
      flex-direction: column;
    }
    &__text {
      padding-bottom: 45px;
    }
    &__card {
      width: 100%;
    }
  }
  .target {
    &__step {
      flex-direction: column;
      &-item {
        padding-bottom: 40px;
      }
    }
  }
  .client {
    &__brands {
      justify-content: center;
    }
  }
}

@media (max-width: 768px) {
  .header {
    &__bottom {
      flex-direction: column;
      &-text {
        text-align: start;
        padding-top: 30px;
      }
    }
  }
  .about {
    flex-direction: column;
  }
  .target {
    &__logo {
      margin-bottom: 45px;
    }
    &__heading {
      flex-direction: column;
      &-title {
        text-align: center;
      }
    }
  }
  .contact {
    .container {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      text-align: center;
      h3 {
        margin-bottom: 45px;
      }
    }
  }
}

@media (max-width: 672px) {
  .services {
    &__item {
      flex-basis: 100%;
    }
    &__text-grid {
      flex-direction: column;
      p:first-child {
        padding-bottom: 45px;
      }
    }
  }
  .price {
    &__card {
      flex-direction: column;
    }
  }
  .card {
    flex-basis: 100%;
    &:first-child {
      margin-bottom: 15px;
    }
  }
  .client {
    &__text-grid {
      flex-direction: column;
      div:first-child {
        margin-bottom: 45px;
      }
    }
  }
  .footer {
    .container {
      flex-direction: column;
    }
    .tel {
      margin-bottom: 15px;
    }
    &__info {
      &-top,
      &-bottom {
        text-align: left;
      }
    }
  }
}

@media (max-width: 478px) {
  h2 {
    font-size: 40px;
  }
  h3 {
    font-size: 24px;
  }
  h4 {
    font-size: 20px;
  }
  .button {
    &__price {
      margin: 15px 0 0 0;
      width: 100%;
    }
  }
  .header {
    &__title {
      font-size: 44px;
    }
    &__bottom-text {
      text-align: center;
    }
  }
  .slide-menu {
    max-width: 100%;
    width: 100%;
  }
  .services,
  .header {
    button {
      width: 100%;
    }
  }
  .target {
    &__logo {
      img {
        width: 120px;
      }
    }
  }
}
