$fontFamily: "Montserrat", sans-serif;
$font: 18px;
$fontSmall: 14px;
$fontBig: 24px;

$color: #001e42;
$colorAccent: #00feff;

* {
  box-sizing: border-box;
}
html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}
body {
  font-family: $fontFamily;
  font-size: $font;
  color: #252d37;
  line-height: 1.4;
  position: relative;
}
//Global properties
a,
a:hover {
  color: inherit;
  text-decoration: none;
}

input,
textarea,
button {
  &:hover,
  &:focus {
    outline: none;
  }
}
h1,
h2 {
  font-size: 50px;
}
h2 {
  font-weight: 700;
}

h2,
h4 {
  margin-bottom: 45px;
  color: $color;
}

h4 {
  font-size: 24px;
  margin-top: 0;
}
h3 {
  font-size: 36px;
  color: #fff;
  font-weight: 700;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  display: flex;
  justify-content: center;
}

.muted {
  color: #93a0ad;
}

.button {
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  padding: 14px;
  &__price {
    border: 2px solid $color;
    color: $color;
    background: transparent;
    position: relative;
    margin-right: 15px;
    padding-right: 30px;
    img {
      position: absolute;
      top: 50%;
      right: 5px;
      width: 22px;
      transform: translateY(-50%);
    }
    &::after {
      transition: 0.2s all ease;
      content: "";
      position: absolute;
      top: -5px;
      left: -5px;
      height: calc(100% + 10px);
      width: 0;
      background-color: #e5e5e5;
      z-index: -1;
    }
    &:hover {
      &::after {
        width: calc(100% + 10px);
      }
    }
  }
  &__submit {
    background: $colorAccent;
    border: 2px solid $colorAccent;
    color: $color;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      transition: 0.2s all ease;
      margin-left: 10px;
    }
    &:hover {
      img {
        margin-left: 30px;
      }
    }
  }
}

.header {
  background-image: linear-gradient(
      0deg,
      rgba(1, 53, 113, 0.25),
      rgba(1, 53, 113, 0.25)
    ),
    url("../img/header-bg.jpg");
  background-color: $color;
  background-position: center;
  background-size: cover;
  color: #fff;
  & > .container {
    flex-direction: column;
  }
  &__title {
    padding: 90px 0 50px;
  }
  &__title,
  &__subtitle {
    font-weight: 900;
    color: $colorAccent;
  }
  &__subtitle {
    padding-bottom: 30px;
  }
  &__info {
    font-size: 24px;
    & + p {
      padding-bottom: 30px;
    }
  }
  &__span {
    font-weight: 700;
    font-size: 24px;
    line-height: 0;
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    padding-bottom: 60px;
    &-text {
      max-width: 460px;
      text-align: end;
      font-size: 14px;
    }
  }
}

.nav {
  padding-top: 30px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: 0.2s all ease;
  z-index: 999;
  &.scrolled {
    padding: 15px 0;
    .nav__wrap {
      align-items: center;
    }
  }
  &.hide {
    top: -80px;
  }
  &__wrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    &.scrolled {
      align-items: center;
    }
  }
  &__logo {
    cursor: pointer;
    height: 45px;
  }
  &__burger {
    cursor: pointer;
    display: none;
    z-index: 99999;
    height: 2px;
    width: 27px;
    background: $colorAccent;
    position: relative;
    &,
    &::after,
    &::before {
      transition: 0.2s all ease;
    }
    &::after,
    &::before {
      content: "";
      position: absolute;
      background: $colorAccent;
      width: 27px;
      height: 2px;
    }
    &::after {
      top: 9px;
    }
    &::before {
      bottom: 9px;
    }
    &.active {
      background-color: transparent;
      &::before,
      &::after {
        background-color: $color;
      }
      &::after {
        transform: rotate(45deg);
        top: 0;
      }
      &::before {
        transform: rotate(-45deg);
        bottom: 0;
      }
    }
  }
  &__link {
    margin: 0 20px;
    position: relative;

    &::after {
      transition: 0.2s all ease;
      content: "";
      position: absolute;
      height: 2px;
      width: 0;
      background-color: $colorAccent;
      left: 50%;
      bottom: -5px;
    }
    &:hover {
      &::after {
        left: 0;
        width: 100%;
      }
    }
  }
  .tel {
    &__operator {
      padding-right: 12px;
    }
  }
}

.nav-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.nav-overlay {
  top: -75px;
  left: 0;
  right: 0;
  height: 75px;
  z-index: 9;
  position: fixed;
  transition: 0.3s all ease;
  &.to-up {
    top: 0;
    background-color: $color;
  }
}

.tel {
  display: flex;
  flex-direction: column;
  &__item {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__operator {
    font-size: 12px;
  }
  &__phone {
    font-weight: 700;
  }
}

.about {
  padding-top: 90px;
  &__title {
    flex-basis: 25%;
  }
  &__text {
    flex-basis: 75%;
  }
}

.services {
  padding-top: 90px;
  flex-direction: column;
  &__grid {
    padding-bottom: 90px;
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    display: flex;
    align-items: center;
    flex-basis: 33.33%;
    padding: 20px;
    overflow: hidden;
    position: relative;
    &::after {
      transition: 0.3s all ease;
      content: "";
      position: absolute;
      width: 1px;
      height: 100%;
      background-color: $colorAccent;
      z-index: -1;
      opacity: 0;
      top: 0;
      left: 0;
    }
    &:hover {
      .services__logo {
        transform: scale(1.2);
      }
      &::after {
        width: 100%;
        opacity: 1;
      }
    }
  }
  &__logo {
    padding-right: 20px;
    transition: 0.2s all ease;
  }
  &__label {
    font-weight: 700;
    color: $color;
  }
  &__text-grid {
    display: flex;
    justify-content: space-between;
    margin-bottom: 45px;
    p {
      flex-basis: calc(50% - 15px);
    }
  }
}

.services,
.header {
  button {
    width: 350px;
  }
}

.target {
  padding-top: 90px;
  flex-direction: column;
  &__heading,
  &__logo {
    display: flex;
    align-items: center;
    img {
      width: 200px;
    }
    h2 {
      margin: 0 0 0 15px;
    }
  }
  &__heading {
    padding-bottom: 90px;
    justify-content: space-between;
    h4 {
      margin: 0;
    }
  }
  &__heading-title,
  &__logo {
    flex-basis: calc(50% - 15px);
  }
  &__logo {
    justify-content: center;
  }
  &__step {
    display: flex;
    &-item {
      flex-basis: 33.33%;
      font-weight: 700;
      color: $color;
      position: relative;
      padding: 40px 40px 0;
      &::before {
        content: attr(data-index);
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        color: $colorAccent;
        font-size: 96px;
      }
    }
  }
}

.price {
  padding-top: 90px;
  flex-direction: column;
  &__grid {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__text,
  &__card {
    flex-basis: calc(50% - 15px);
  }
  &__card {
    display: flex;
    justify-content: space-between;
  }
  &__text {
    p:first-child {
      padding-bottom: 45px;
    }
    p:nth-child(2) {
      padding-bottom: 15px;
    }
  }
}

.card {
  color: $color;
  border: 2px solid $color;
  text-align: center;
  flex-basis: calc(50% - 15px);
  padding: 40px 0;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  &::after {
    transition: 0.5s all ease;
    content: "";
    z-index: -1;
    position: absolute;
    height: 150%;
    width: 100px;
    background-color: #f2f2f2;
    transform: rotate(-35deg);
    left: -50%;
    top: 50%;
  }
  &:hover {
    &::after {
      left: 0;
      top: 0;
    }
  }
  &__normal,
  &__bold {
    font-weight: 700;
    text-transform: uppercase;
  }
  &__normal {
    font-size: 26px;
  }
  &__bold {
    font-size: 70px;
  }
  &__light {
    margin-bottom: 16px;
  }
}

.client {
  padding-top: 90px;
  padding-bottom: 90px;
  flex-direction: column;
  &__brands {
    padding-bottom: 90px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    img {
      transition: 0.2s all ease;
      filter: grayscale(100);
      width: 160px;
      cursor: pointer;
      &:hover {
        filter: grayscale(0);
      }
    }
  }
  &__text-grid {
    display: flex;
    justify-content: space-between;
    font-size: 13px;
    & > div {
      flex-basis: calc(50% - 15px);
    }
    ul {
      list-style: none;
      padding-inline-start: 15px;
      li {
        position: relative;
      }
      li::before {
        content: "-";
        position: absolute;
        left: -15px;
      }
    }
  }
}

.confirm {
  user-select: none;
  cursor: pointer;
  position: relative;
  padding-left: 15px;
  &__checkbox {
    opacity: 0;
  }
  &__mask {
    position: absolute;
    height: 18px;
    width: 18px;
    background: #fff;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    &::after {
      content: "";
      position: absolute;
      left: 5px;
      top: 1px;
      width: 5px;
      height: 10px;
      border: solid #000;
      border-width: 0 3px 3px 0;
      transform: rotate(45deg);
      display: none;
    }
  }
  &__checkbox:checked ~ &__mask::after {
    display: block;
  }
}

.contact {
  padding-top: 45px;
  padding-bottom: 45px;
  background-image: linear-gradient(
      0deg,
      rgba(1, 53, 113, 0.25),
      rgba(1, 53, 113, 0.25)
    ),
    url(../img/contact-bg.jpg);
  background-color: $color;
  background-position: center center;
  background-size: cover;
  .container {
    justify-content: space-between;
  }
  &__item {
    flex-basis: calc(50% - 15px);
    h3 {
      margin: 0;
    }
    &-flex {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  &__form {
    display: flex;
    flex-direction: column;
    max-width: 350px;
  }
  button {
    width: 100%;
  }
}

.form {
  &__input {
    border: 2px solid #fff;
    padding: 8px 15px;
    margin-bottom: 15px;
  }
  &__input:focus {
    border-bottom: 2px solid $colorAccent;
  }
  &__area {
    resize: none;
  }
  &__file {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer;
    #file {
      display: none;
    }
    &-text {
      margin-left: 15px;
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 700;
      color: #fff;
    }
  }
  &__confirm {
    font-size: 10px;
    margin-bottom: 15px;
  }
}

.footer {
  padding: 30px 0;
  background-color: #06182f;
  .container {
    justify-content: space-between;
  }
  &__contacts {
    color: #fff;
    .mail {
      display: inline-block;
      position: relative;
      margin-bottom: 15px;
      &::after {
        transition: 0.2s all ease;
        content: "";
        position: absolute;
        height: 2px;
        width: 0;
        background-color: $colorAccent;
        left: 50%;
        bottom: -5px;
      }
      &:hover {
        color: $colorAccent;
        &::after {
          left: 0;
          width: 100%;
        }
      }
    }
    .tel {
      &__item {
        justify-content: flex-start;
      }
      &__phone {
        padding-right: 12px;
      }
    }
  }
  &__info {
    text-align: end;
    &-top {
      color: $colorAccent;
      max-width: 300px;
      margin-bottom: 15px;
    }
    &-bottom {
      color: #fff;
    }
  }
}

.slide-menu {
  max-width: 400px;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 99;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transition: 0.3s all ease;
  color: $color;
  clip-path: polygon(100% 0, 100% 0%, 100% 100%, 100% 100%);
  &.active {
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
  }
  &__logo {
    max-width: 180px;
    position: absolute;
    top: 40px;
  }
  .tel {
    &__item {
      justify-content: flex-start;
    }
    &__phone {
      padding-right: 15px;
    }
  }
  &__link {
    padding-bottom: 40px;
  }
  &__button {
    position: absolute;
    bottom: 40px;
    width: calc(100% - 80px);
  }
  &__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
